import React from 'react';
import styles from './styles.module.scss';
const bg = require('@/assets/img/bannerbg.png').default;

function Banner({title, conts, height}) {
  return (
    <div
      className={styles['wrap']}
      style={{
        backgroundImage: `url(${bg})`,
        height,
      }}
    >
      <div className={styles['banner']}>
        <h2>{title}</h2>
        {conts.map((item) => (
          <p key={item.id}>{item.cont}</p>
        ))}
      </div>
    </div>
  );
}
export default Banner;
